<template>
  <div class="page page_404">
    <div class="container-fluid">
      <div class="error-border">
        <div class="row">
          <div class="col-md-12 text-center">
            <h1 class="error-title">{{page_404[$route.params.lang]['title']}}</h1>
            <h2 class="error-subtitle">{{page_404[$route.params.lang]['subtitle']}}</h2>
            <div class="button-wrap">
              <a @click="goToHome" class="error-button button__full button-border__regular">{{page_404[$route.params.lang]['button']}}</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
	export default {
		data(){
			return {
				page_404: {
					ru: {
						title: '404',
						subtitle: 'Страница не найдена',
						button: 'На главную'
					},
					en: {
						title: '404',
						subtitle: 'Page not found',
						button: 'Go home'
					}
				}
			}
		},
		methods: {
			goToHome(){
				this.$router.push('/' + this.$route.params.lang);
			}
		}
	}
</script>

<style lang="sass" src="./../assets/style/page404.sass"></style>



